import { ClientData } from './clientData';
import { Injectable } from '@angular/core';
import {HttpClient} from  '@angular/common/http';
import { Observable } from 'rxjs';

const url='avi';
@Injectable({
  providedIn: 'root'
})
export class SignatureService {

  loginRnum:String;
 
  constructor(private http:HttpClient) { }

  getData():Observable<ClientData[]>{
    return this.http.get<ClientData[]>(url)
  }
  putData(data:ClientData,rNum:any){
    
    this.http.put(`${url}/${rNum}`, data).subscribe(res => {
    });
   
  }
  async getDataWithParam(rNum:String){
    return await this.http.get(`${url}/${rNum}`).toPromise();
  }
}  
