import { ClientData } from './clientData';
import { SignatureService } from './signature.service';

import { Component } from '@angular/core';

@Component({ 
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'vsignatures';
  tshirtb:boolean=false
  commentb:boolean=false
  mb:boolean=true
  
  rname:String="";
  rRnum:String="";
  rPass:String="";
  rcPass:String="";
  lRnum:String="";
  lPass:String="";
  commentRnum:String="";
  comment :string="";
  rb1:boolean=false;
  rb2:boolean=false;
  rtolb:boolean=false;
  data:ClientData=new ClientData();

  v:boolean=true;
  l:boolean=false;
  r:boolean=false;
  lb0:boolean=false;
  le:boolean=false;

  constructor(private service:SignatureService){}
 
  m(){
this.v=false
this.l=true
this.r=false
this.rb1=false
this.rb2=false
this.lb0=false
this.le=false;
this.tshirtb=false
this.commentb=false
  }
  m1(){
    this.v=true
    this.mb=true
    this.l=false
    this.r=false
    this.rb1=false
    this.rb2=false
    this.lb0=false
    this.le=false;
    this.tshirtb=false
    this.commentb=false
      }
   m2(){
     this.r=true;
     this.l=false;
     this.v=false
     this.rb1=false
     this.rb2=false
     this.lb0=false
     this.le=false;
     this.tshirtb=false
     this.commentb=false
   }   


   async register(){

 if(this.rname=="" || this.rRnum=="" || this.rPass=="" || this.rcPass==""){
 this.rb1=true
 }
 else if(this.rPass!=this.rcPass)
{
  this.rb2=true
   
}  
else {
  this.rb1=false
  this.rb2=false
}
if(this.rb1==false && this.rb2==false){
  let a:any=await this.service.getDataWithParam(this.rRnum)
  if(a.length==0 ){
  this.data.name=this.rname;
  this.data.rNum=this.rRnum;
  this.data.password=this.rPass; 
  
  this.service.putData(this.data,this.rRnum)
  this.rtolb=true
  }
  else if(a[0].name==""){
    this.data.name=this.rname;
  this.data.rNum=this.rRnum;
  this.data.password=this.rPass; 
  
  this.service.putData(this.data,this.rRnum)
  this.rtolb=true

  }
  else{
    alert("User with this Roll number already exists.Contact Admin!")
  }
}

 } 
  



   async login(){
   
    
  this.l=true
  this.v=false
  this.rb1=false
  this.rb2=false
  this.r=false
  this.lb0=false 
  this.le=false;
  this.tshirtb=false
  this.commentb=false
 
 
 
    let a:any=await this.service.getDataWithParam(this.lRnum)
       
  if(a.length!=0){
    if(a[0].rNum==this.lRnum && a[0].password==this.lPass ){
    this.lb0=true
    this.commentb=true
    this.l=false
    this.le=false
    this.service.loginRnum=this.lRnum
    this.mb=false
    this.tshirtb=false
    }
    else{
      this.le=true;
    }
     
    

  }
  else{
    this.le=true;
  }
   
   
     

  } 
  rtol(){
    this.l=true
    this.v=false
    this.rb1=false
    this.rb2=false
    this.r=false
    this.lb0=false
    this.rtolb=false
    this.le=false
    this.tshirtb=false
    this.commentb=false
  }
  
  tshirt(){
    this.tshirtb=true
    this.commentb=false
    this.rb1=false
  }
  lfirstpage(){
    this.commentb=true
    this.lb0=true
    this.tshirtb=false
    this.rb1=false
  }
  
async submitComment(){

if( this.comment=="" || this.commentRnum=="")
{
this.rb1=true
}
else{
  this.rb1=false
  let a:any=await this.service.getDataWithParam(this.commentRnum)
  let b:any=await this.service.getDataWithParam(this.lRnum)
  this.data=new ClientData();
  if(a.length==0){
   
    this.data.c1=this.comment+" " + "-" +b[0].name;
    this.data.rNum=this.commentRnum;
    this.data.counter=1;
    let tempArray=a[0].commentArray
    tempArray.push(this.lRnum)
    this.data.commentArray=tempArray
  this.service.putData(this.data,this.commentRnum)
  alert("Comment submitted, ask your friend to check his t-shirt xD ")
  }
  else{
    if(a[0].counter==16){
      alert("Maximum comments for this t-shirt has exceeded.Contact the admin ")
    }
    else{
      let stop=0;
     for(var i=0;i<a[0].commentArray.length;i++){
       if( a[0].commentArray[i]==this.lRnum){
         stop=1
         break
       }
     }
     if(stop==1){
       alert("You've already commented for this RollNumber")
     }
     else{
      var t="c"+(a[0].counter+1).toString()
      this.data[t]=this.comment+" " + "-" +b[0].name;

      this.data.counter=a[0].counter+1
      let tempArray=a[0].commentArray
      tempArray.push(this.lRnum)
      this.data.commentArray=tempArray
      this.service.putData(this.data,this.commentRnum)
      alert("Comment submitted, ask your friend to check his t-shirt xD ")
     }
  
    }
  }
  
}
  }
}
